.home-container {
  max-width: 1200px;
  min-width: 80vw;
  margin: 0 auto;
  padding: 20px;
  font-family: 'Inter', 'Segoe UI', 'Roboto', sans-serif;
  color: #111827;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Center content when not searched */
.home-container:not(.has-searched) {
  justify-content: center;
}

/* Make sure the footer always stays at the bottom */
.home-container {
  display: flex;
  flex-direction: column;
}

.results-section {
  flex: 1;
}

.header {
  text-align: center;
  margin: 40px 0 60px;
  transition: all 0.5s ease;
}

.home-container.has-searched .header {
  margin: 20px 0 30px;
}

.header h1 {
  color: #111827;
  font-size: 3rem;
  font-weight: 800;
  margin-bottom: 0.5rem;
  background: linear-gradient(90deg, #1a56db, #7e3af2);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  letter-spacing: -0.025em;
  transition: font-size 0.5s ease;
}

.home-container.has-searched .header h1 {
  font-size: 2.5rem;
}

.subtitle {
  color: #6b7280;
  font-size: 1.25rem;
  max-width: 600px;
  margin: 0 auto;
  transition: font-size 0.5s ease;
}

.home-container.has-searched .subtitle {
  font-size: 1rem;
}

.search-section {
  margin-bottom: 40px;
  position: relative;
  transition: all 0.5s ease;
}

.search-section-top {
  margin-top: 0;
  margin-bottom: 20px;
}

.search-box {
  display: flex;
  max-width: 800px;
  margin: 0 auto;
  border-radius: 16px;
  box-shadow: 0 10px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  transition: all 0.3s ease;
  border: 1px solid #e5e7eb;
}

.search-box:focus-within {
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  border-color: #c7d2fe;
}

.search-input {
  flex: 1;
  padding: 20px 24px;
  font-size: 1.125rem;
  border: none;
  border-radius: 16px 0 0 16px;
  outline: none;
  transition: background-color 0.3s;
  background-color: #fff;
  color: #111827;
  -webkit-appearance: none;
}

.search-input::placeholder {
  color: #9ca3af;
}

.search-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 70px;
  background: linear-gradient(90deg, #1a56db, #7e3af2);
  color: white;
  border: none;
  border-radius: 0 16px 16px 0;
  cursor: pointer;
  transition: all 0.3s ease;
  -webkit-appearance: none;
}

/* Mobile-specific search box adjustments */
@media (max-width: 480px) {
  .search-box {
    display: flex;
    flex-direction: row; /* Keep horizontal layout */
    height: auto;
  }
  
  .search-input {
    border-radius: 16px 0 0 16px;
    width: calc(100% - 60px);
    font-size: 16px; /* Prevent iOS zoom on input focus */
  }
  
  .search-button {
    width: 60px;
    min-width: 60px;
    border-radius: 0 16px 16px 0;
  }
}

.search-button:hover {
  opacity: 0.9;
}

.search-button:disabled {
  /* Keep gradient background even when disabled */
  background: linear-gradient(90deg, #1a56db, #7e3af2);
  opacity: 0.8;
  cursor: wait;
}

.spinner {
  width: 20px;
  height: 20px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: #fff;
  animation: spin 0.8s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.suggestions-dropdown {
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  width: 800px;
  max-width: 100%;
  background-color: white;
  border-radius: 12px;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  z-index: 10;
  margin-top: 8px;
  overflow: hidden;
  animation: fadeIn 0.2s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateX(-50%) translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateX(-50%) translateY(0);
  }
}

.suggestion-item {
  padding: 12px 20px;
  cursor: pointer;
  transition: background-color 0.2s;
  border-bottom: 1px solid #f3f4f6;
}

.suggestion-item:hover {
  background-color: #f9fafb;
}

.suggestion-item:last-child {
  border-bottom: none;
}

.random-suggestions {
  text-align: center;
  margin-top: 30px;
  visibility: hidden;
  opacity: 0;
}

.random-suggestions.fade-in {
  visibility: visible;
  opacity: 1;
  /* animation: fadeIn 0.5s ease-out forwards; */
}

.random-suggestions p {
  color: #6b7280;
  margin-bottom: 12px;
  font-size: 0.95rem;
}

.suggestion-tags {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}

.suggestion-tag {
  display: inline-block;
  padding: 8px 16px;
  background-color: #f3f4f6;
  color: #4b5563;
  border-radius: 20px;
  font-size: 0.875rem;
  transition: all 0.2s;
  cursor: pointer;
  border: 1px solid #e5e7eb;
}

.suggestion-tag:hover {
  background-color: #e5e7eb;
  transform: translateY(-2px);
}

.results-section {
  margin-top: 60px;
  padding-top: 20px;
  opacity: 0;
  transform: translateY(20px);
  transition: all 0.5s ease;
  display: none;
}

.results-section.visible {
  opacity: 1;
  transform: translateY(0);
  display: block;
}

.results-section h2 {
  color: #111827;
  border-bottom: 2px solid #e5e7eb;
  padding-bottom: 10px;
  margin-bottom: 30px;
  font-weight: 700;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.search-metrics {
  font-size: 0.875rem;
  color: #6b7280;
  font-weight: normal;
  margin-left: auto;
}

.no-results {
  text-align: center;
  color: #6b7280;
  font-size: 1.125rem;
  margin: 60px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.results-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(360px, 1fr));
  gap: 30px;
}

.paper-card {
  background-color: #fff;
  border-radius: 16px;
  padding: 25px;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  height: 100%;
  border: 1px solid #f3f4f6;
  position: relative;
  overflow: hidden;
  word-break: break-word; /* Ensure long titles don't overflow */
}

@media (max-width: 480px) {
  .paper-card {
    padding: 16px;
    margin-bottom: 15px;
    border-radius: 12px;
  }
}

.paper-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 4px;
  background: linear-gradient(90deg, #1a56db, #7e3af2);
  opacity: 0;
  transition: opacity 0.3s ease;
}

/* Special styling for perfect match cards */
.paper-card[data-perfect="true"]::before {
  background: linear-gradient(90deg, #059669, #10b981);
  opacity: 1;
}

.paper-card[data-perfect="true"] {
  border-color: #dcfce7;
  box-shadow: 0 10px 20px -5px rgba(5, 150, 105, 0.15), 0 8px 10px -5px rgba(5, 150, 105, 0.1);
}

.paper-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

.paper-card:hover::before {
  opacity: 1;
}

.paper-meta {
  display: flex;
  gap: 10px;
  margin-bottom: 15px;
  flex-wrap: wrap;
}

.paper-year,
.paper-conference,
.paper-match-score {
  display: inline-block;
  padding: 4px 10px;
  background-color: #f3f4f6;
  border-radius: 12px;
  font-size: 0.75rem;
  font-weight: 600;
  color: #4b5563;
}

.paper-match-score {
  background-color: #eef2ff;
  color: #4f46e5;
  margin-left: auto;
  position: relative;
  overflow: hidden;
}

/* Perfect match indicator */
.paper-match-score[data-perfect="true"] {
  background-color: #dcfce7; /* light green */
  color: #166534; /* dark green */
  font-weight: 700;
}

/* High match indicator */
.paper-match-score[data-high="true"] {
  background-color: #e0f2fe; /* light blue */
  color: #0369a1; /* dark blue */
}

/* Add subtle animation for perfect matches */
@keyframes pulse {
  0% { box-shadow: 0 0 0 0 rgba(22, 101, 52, 0.4); }
  70% { box-shadow: 0 0 0 6px rgba(22, 101, 52, 0); }
  100% { box-shadow: 0 0 0 0 rgba(22, 101, 52, 0); }
}

.paper-match-score[data-perfect="true"] {
  animation: pulse 2s infinite;
}

.paper-title {
  color: #111827;
  margin: 0 0 15px 0;
  font-size: 1.25rem;
  line-height: 1.4;
  font-weight: 700;
}

.paper-authors {
  color: #6b7280;
  font-size: 0.875rem;
  margin-bottom: 15px;
  line-height: 1.5;
}

.paper-abstract {
  color: #4b5563;
  line-height: 1.6;
  margin-bottom: 15px;
  font-size: 0.95rem;
  flex-grow: 1;
}

.abstract-toggle {
  background: none;
  border: none;
  color: #4f46e5;
  font-size: 0.875rem;
  cursor: pointer;
  padding: 0;
  margin-bottom: 15px;
  transition: color 0.2s;
  text-align: left;
  font-weight: 600;
}

.abstract-toggle:hover {
  color: #6366f1;
  text-decoration: underline;
}

.paper-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-bottom: 20px;
}

.tag {
  background-color: #eff6ff;
  color: #1d4ed8;
  padding: 4px 10px;
  border-radius: 12px;
  font-size: 0.75rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.tag:hover {
  background-color: #dbeafe;
  transform: translateY(-2px);
}

.paper-link {
  display: inline-block;
  padding: 8px 16px;
  background: linear-gradient(90deg, #1a56db, #4f46e5);
  color: white;
  text-decoration: none;
  border-radius: 8px;
  font-size: 0.875rem;
  font-weight: 500;
  transition: all 0.2s ease;
  margin-top: auto;
  text-align: center;
  pointer-events: painted;
}

.paper-link:hover {
  opacity: 0.9;
  transform: translateY(-2px);
}

/* Animation for cards entering viewport */
@keyframes cardFadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.paper-card {
  animation: cardFadeIn 0.5s ease-out;
  animation-fill-mode: both;
}

.results-list .paper-card:nth-child(1) { animation-delay: 0.1s; }
.results-list .paper-card:nth-child(2) { animation-delay: 0.2s; }
.results-list .paper-card:nth-child(3) { animation-delay: 0.3s; }
.results-list .paper-card:nth-child(4) { animation-delay: 0.4s; }
.results-list .paper-card:nth-child(5) { animation-delay: 0.5s; }
.results-list .paper-card:nth-child(6) { animation-delay: 0.6s; }

/* Responsive adjustments */
@media (max-width: 768px) {
  .home-container {
    padding: 16px;
  }
  
  .header {
    margin: 30px 0 40px;
  }
  
  .home-container.has-searched .header {
    margin: 15px 0 20px;
  }
  
  .header h1 {
    font-size: 2.25rem;
  }
  
  .home-container.has-searched .header h1 {
    font-size: 1.75rem;
  }
  
  .subtitle {
    font-size: 1rem;
    padding: 0 10px;
  }
  
  .home-container.has-searched .subtitle {
    font-size: 0.875rem;
  }
  
  .search-box {
    width: 95%;
  }
  
  .search-input {
    padding: 16px;
    font-size: 1rem;
  }
  
  .results-list {
    grid-template-columns: 1fr;
  }
  
  .suggestions-dropdown {
    width: 95%;
  }
  
  .paper-card {
    padding: 20px;
  }
  
  .paper-meta {
    gap: 8px;
  }
  
  .paper-title {
    font-size: 1.1rem;
  }
}

/* Additional mobile optimizations for smaller screens */
@media (max-width: 480px) {
  .header h1 {
    font-size: 1.8rem;
  }
  
  .home-container.has-searched .header h1 {
    font-size: 1.5rem;
  }
  
  .subtitle {
    font-size: 0.9rem;
  }
  
  .search-input {
    padding: 14px;
  }
  
  .paper-meta {
    flex-wrap: wrap;
    gap: 6px;
  }
  
  .paper-year,
  .paper-conference,
  .paper-match-score {
    font-size: 0.7rem;
    padding: 3px 8px;
  }
  
  .suggestion-tags {
    gap: 8px;
    justify-content: center;
  }
  
  .suggestion-tag {
    padding: 6px 12px;
    font-size: 0.8rem;
  }
  
  .random-suggestions p {
    font-size: 0.85rem;
  }
  
  .paper-abstract {
    font-size: 0.9rem;
  }
  
  .paper-authors {
    font-size: 0.8rem;
  }
  
  .tag {
    font-size: 0.7rem;
    padding: 3px 8px;
  }
  
  .abstract-toggle {
    font-size: 0.8rem;
  }
  
  .paper-link {
    font-size: 0.8rem;
    padding: 8px 12px;
  }
}

/* Mobile touch improvements */
@media (max-width: 768px) {
  /* Improve touch targets */
  .suggestion-item, 
  .suggestion-tag, 
  .tag, 
  .abstract-toggle,
  .paper-link {
    padding-top: 10px;
    padding-bottom: 10px;
    touch-action: manipulation;
  }
  
  /* Fix iOS touch delay */
  a, button, input, select, textarea, label {
    touch-action: manipulation;
  }
  
  /* Fix iOS momentum scrolling */
  .suggestions-dropdown {
    -webkit-overflow-scrolling: touch;
  }
  
  /* Prevent pull-to-refresh interference */
  html, body {
    position: fixed;
    width: 100%;
    height: 100%;
    overflow: auto;
    touch-action: pan-y;
    -webkit-overflow-scrolling: touch;
  }
  
  /* Footer adjustments for mobile */
  .site-footer {
    padding: 15px 10px;
  }
  
  .copyright, .support {
    font-size: 0.8rem;
  }
}

/* Footer styling */
.site-footer {
  margin-top: 60px;
  padding: 20px 0;
  text-align: center;
  border-top: 1px solid #e5e7eb;
  color: #6b7280;
}

.footer-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
}

.copyright, .support {
  font-size: 0.875rem;
  margin: 0;
}

.support {
  font-weight: 500;
  background: linear-gradient(90deg, #1a56db, #7e3af2);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

@media (max-width: 768px) {
  .site-footer {
    margin-top: 40px;
    padding: 15px 0;
  }
}

/* Font import */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700;800&display=swap');